//
// Colors
// http://ui-toolkit.rdstation.com.br/front-end/colors
// --------------------------------------------------

// Body color
$body-bg:                #E8EBF1;

// Brand colors
$brand-primary:          #31a2ff;
$brand-secondary:        #364a65;
$brand-success:          #01bc8b;
$brand-info:             #4299e0;
$brand-warning:          #ffbd02;
$brand-danger:           #fb5b41;

// Grays
$gray-medium-dark:       lighten(#000, 28%);
$gray-medium-light:      lighten(#000, 40%);
$gray-light-medium:      lighten(#000, 70%);
$gray-lighter:           #eee;

// Social Media colors
$brand-twitter:          #00aced;
$brand-facebook:         #3B5998;
$brand-linkedin:         #1B86BB;
$brand-analytics:        #f56e2b;
$brand-googleplus:       #dd4b39;

// Legend chart colors
$brand-chart-1:          #4c66b7;
$brand-chart-2:          #7ca932;
$brand-chart-3:          #ddb606;
$brand-chart-4:          #b94c2e;
$brand-chart-5:          #7e4e9c;
$brand-chart-6:          #737373;
$brand-chart-7:          #70a5c2;
$brand-chart-8:          #9fbe3e;
$brand-chart-9:          #e59b5a;
$brand-chart-10:         #c76689;
$brand-chart-11:         #7b6db4;
$brand-chart-12:         #a5a5a5;

//
// Typography
// http://ui-toolkit.rdstation.com.br/front-end/typography
// --------------------------------------------------

// Font family
$font-family-sans-serif:    'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;

// Font size
$font-size-base:            16px;
$font-size-xsmall:          ceil(($font-size-base * 0.8125)); // ~13px
$font-size-medium:          ceil(($font-size-base * 1.1250)); // ~18px
$font-size-mlarge:          ceil(($font-size-base * 1.6870)); // ~27px
$font-size-xlarge:          ceil(($font-size-base * 2.0000)); // ~32px


// Scaffolding
//
// --------------------------------------------------

//Page Header
$page-header-bg:               #FFFFFF;
$page-header-padding:          20px 15px;
$page-header-title-color:      $brand-secondary;
$page-header-title-size:       24px;
$page-header-subtitle-color:   #98A2B0;
$page-header-subtitle-size:    18px;

//Main Content
$main-content-bg:              #FFFFFF;
$main-content-border-color:    #D9DDE4;
$main-content-border-radius:   4px;
$main-content-padding:         15px 0;


//
// Tables
// http://ui-toolkit.rdstation.com.br/front-end/tables
// --------------------------------------------------

// Table cell padding
$table-expanded-cell-padding:       15px;

// Table backgrounds
$table-bg:                          #f4f5f8;
$table-bg-filter:                   $table-bg;
$table-bg-thead:                    transparent;
$table-bg-tbody:                    white;
$table-bg-tfoot:                    transparent;
$table-bg-hover:                    #f4f5f8;


//
// Buttons
// http://ui-toolkit.rdstation.com.br/front-end/buttons
// --------------------------------------------------

// Button size
$btn-border-radius-base:            2px;
$btn-border-radius-large:           $btn-border-radius-base;
$btn-border-radius-small:           $btn-border-radius-base;

// Button colors (text color, background and border)
$btn-default-color:                 $brand-primary;
$btn-default-bg:                    #fff;
$btn-default-border:                $brand-primary;

$btn-primary-color:                 #fff;
$btn-primary-bg:                    $brand-primary;
$btn-primary-border:                $btn-primary-bg;

$btn-success-color:                 #fff;
$btn-success-bg:                    $brand-success;
$btn-success-border:                $btn-success-bg;

$btn-info-color:                    $brand-info;
$btn-info-bg:                       #fff;
$btn-info-border:                   $btn-info-color;

$btn-warning-color:                 #fff;
$btn-warning-bg:                    $brand-warning;
$btn-warning-border:                $btn-warning-bg;

$btn-danger-color:                  #fff;
$btn-danger-bg:                     $brand-danger;
$btn-danger-border:                 $btn-danger-bg;

$btn-secundary-color:               $brand-secondary;
$btn-secundary-bg:                  #fff;
$btn-secundary-border:              darken($btn-secundary-bg, 5%);

$btn-chart-1-color:                 #fff;
$btn-chart-1-bg:                    $brand-chart-1;
$btn-chart-1-border:                darken($btn-chart-1-bg, 5%);

$btn-chart-2-color:                 #fff;
$btn-chart-2-bg:                    $brand-chart-2;
$btn-chart-2-border:                darken($btn-chart-2-bg, 5%);

$btn-chart-3-color:                 #fff;
$btn-chart-3-bg:                    $brand-chart-3;
$btn-chart-3-border:                darken($btn-chart-3-bg, 5%);

$btn-chart-4-color:                 #fff;
$btn-chart-4-bg:                    $brand-chart-4;
$btn-chart-4-border:                darken($btn-chart-4-bg, 5%);

$btn-chart-5-color:                 #fff;
$btn-chart-5-bg:                    $brand-chart-5;
$btn-chart-5-border:                darken($btn-chart-5-bg, 5%);

$btn-chart-6-color:                 #fff;
$btn-chart-6-bg:                    $brand-chart-6;
$btn-chart-6-border:                darken($btn-chart-6-bg, 5%);

$btn-chart-7-color:                 #fff;
$btn-chart-7-bg:                    $brand-chart-7;
$btn-chart-7-border:                darken($btn-chart-7-bg, 5%);

$btn-chart-8-color:                 #fff;
$btn-chart-8-bg:                    $brand-chart-8;
$btn-chart-8-border:                darken($btn-chart-8-bg, 5%);

$btn-chart-9-color:                 #fff;
$btn-chart-9-bg:                    $brand-chart-9;
$btn-chart-9-border:                darken($btn-chart-9-bg, 5%);

$btn-chart-10-color:                #fff;
$btn-chart-10-bg:                   $brand-chart-10;
$btn-chart-10-border:               darken($btn-chart-10-bg, 5%);

$btn-chart-11-color:                #fff;
$btn-chart-11-bg:                   $brand-chart-11;
$btn-chart-11-border:               darken($btn-chart-11-bg, 5%);

$btn-chart-12-color:                #fff;
$btn-chart-12-bg:                   $brand-chart-12;
$btn-chart-12-border:               darken($btn-chart-12-bg, 5%);

//
// Navbar
// http://ui-toolkit.rdstation.com.br/front-end/navbar
// --------------------------------------------------

// Navbar colors
$nav-open-link-hover-color:            #fff;
$navbar-default-color:                 lighten($brand-secondary, 70%);
$navbar-default-bg:                    $brand-secondary;
$navbar-default-border:                darken($navbar-default-bg, 6.5%);

// Navbar links
$navbar-default-link-color:            #D8D8D8;
$navbar-default-link-hover-color:      lighten($navbar-default-link-color, 15%);
$navbar-default-link-hover-bg:         transparent;
$navbar-default-link-active-color:     #FFFFFF;
$navbar-default-link-active-bg:        transparent;
$navbar-default-link-disabled-color:   lighten($navbar-default-bg, 20%);
$navbar-default-link-disabled-bg:      transparent;

// Navbar brand label
$navbar-default-brand-color:           $navbar-default-link-color;
$navbar-default-brand-hover-color:     darken($navbar-default-brand-color, 10%);
$navbar-default-brand-hover-bg:        transparent;

// Navbar toggle
$navbar-default-toggle-hover-bg:       #ddd;
$navbar-default-toggle-icon-bar-bg:    #888;
$navbar-default-toggle-border-color:   #ddd;


//
// Panels
// http://ui-toolkit.rdstation.com.br/front-end/panels
// --------------------------------------------------

// Panel color
$panel-body-bg:               #ffffff;
$panel-border-color:          #d9dde4;
$panel-default-border:        #f4f5f8;
$panel-default-heading-bg:    #f4f5f8;
$panel-footer-bg:             #f4f5f8;

// Panel size
$panel-body-padding:          15px;
$panel-heading-padding:       10px;

// Label colors
$label-beta:                  rgba(136, 184, 64, 0.85);

//
// Popovers
// http://ui-toolkit.rdstation.com.br/front-end/front-end/popovers
// --------------------------------------------------

$popover-bg:                          #fff !default;
$popover-max-width:                   276px !default;
$popover-border-color:                $brand-primary !default;
$popover-border-radius:               2px !default;
$popover-font-size:                   14px !default;
$popover-fallback-border-color:       $brand-primary !default;
$popover-title-bg:                    #e8ebf1 !default;
$popover-title-color:                 $brand-secondary !default;
$popover-arrow-width:                 10px !default;
$popover-arrow-color:                 $popover-bg !default;
$popover-arrow-outer-width:           ($popover-arrow-width + 1) !default;
$popover-arrow-outer-color:           fade_in($popover-border-color, 0.05) !default;
$popover-arrow-outer-fallback-color:  darken($popover-fallback-border-color, 20%) !default;
$popover-padding:                     0 !default;


//
// Slats
// http://ui-toolkit.rdstation.com.br/front-end/slats
// --------------------------------------------------

$slat-bg:                     #ffffff;
$slat-heading-bg:             #ffffff;
$slat-heading-border-color:   #f4f5f8;

//
// Template thumbnails
// TBD
// --------------------------------------------------

$template-thumbnail-padding:           0px;
$template-thumbnail-bg:                #fff;
$template-thumbnail-border:            #ddd;
$template-thumbnail-border-radius:     3px;
$template-thumbnail-hover-bg:          #f7f7f7;
$template-thumbnail-active-border:     $brand-primary;
$template-thumbnail-active-bg:         #f7f7f7;
$template-thumbnail-active-color:      $brand-primary;


//
// Steps color
// http://ui-toolkit.rdstation.com.br/front-end/panels
// --------------------------------------------------

$editor-panel-padding:                 15px;
$editor-panel-width:                   420px;
$editor-panel-width-min:               320px;
$editor-border-color:                  #dddddd;


//
// Z-index Master
// http://ui-toolkit.rdstation.com.br/front-end/panels
// --------------------------------------------------
// Warning: Avoid customizing these values. They're used for a bird's eye view
// of components dependent on the z-axis and are designed to all work together.
//
// Note: These variables are not generated into the Customizer.

$zindex-modal-background:  1040;
$zindex-autocomplete:      1060;


//
// Messages
// http://ui-toolkit.rdstation.com.br/front-end/messages
// --------------------------------------------------

$alert-success-border:                 $brand-success;
$alert-info-border:                    $brand-info;
$alert-warning-border:                 $brand-warning;
$alert-danger-border:                  $brand-danger;


//
// Alerts
// http://ui-toolkit.rdstation.com.br/front-end/alerts
// --------------------------------------------------

$alert-success-bg:                     #c9f1e9;
$alert-success-text:                   #03954e;

$alert-info-bg:                        #d8edf8;
$alert-info-text:                      #226889;

$alert-warning-bg:                     #ffeab9;
$alert-warning-text:                   #f68513;

$alert-danger-bg:                      #ffdfd9;
$alert-danger-text:                    #ff5f3b;


//
// Forms
// http://ui-toolkit.rdstation.com.br/front-end/forms
// --------------------------------------------------

$input-border-radius: 3px;


//
// Modal
// http://ui-toolkit.rdstation.com.br/front-end/modal
// --------------------------------------------------

$modal-content-bg: #f4f5f8;


//
// Navs
// http://ui-toolkit.rdstation.com.br/front-end/navs
// --------------------------------------------------

$nav-tabs-active-link-hover-bg: white;


//
// Sidebar
// http://ui-toolkit.rdstation.com.br/front-end/sidebar
// --------------------------------------------------

// Sidebar
$sidebar-width:                 420px;
$sidebar-index:                 1032;
$sidebar-overlay:               1031;
$sidebar-overlay-color:         #000000;
$sidebar-overlay-opacity:       .40;
$sidebar-color:                 #ffffff;
$sidebar-transition-margin:     margin 0.3s ease-out;
$sidebar-transition-width:      width 0.3s ease-out;
$sidebar-border-width:          1px;
$sidebar-border-style:          solid;
$sidebar-border-color:          #d9dde4;
$sidebar-padding:               15px;

// Sidebar Toggle
$sidebar-toggle-margin:         30px;
$sidebar-toggle-width:          22.5px;
$sidebar-toggle-height:         45px;
$sidebar-toggle-color:          $sidebar-color;
$sidebar-toggle-border-radius:  3px;
$sidebar-toggle-border-color:   $sidebar-border-color;
$sidebar-toggle-text-color:     #777777;

// Sidebar Content
$sidebar-content-margin:        $sidebar-padding;
$sidebar-content-padding:       $sidebar-padding;
$sidebar-content-bg-color:      #E8EBF1;
$sidebar-content-border-radius: 3px;
$sidebar-content-text-color:    $brand-secondary;

// Sidebar Footer
$sidebar-footer-color:          #f4f5f8;
$sidebar-footer-border-width:   1px;
$sidebar-footer-border-type:    solid;
$sidebar-footer-border-color:   #d9dde4;

// Sidebar Resize Handler
$size-handler-bg:               $page-header-bg;
$size-handler-width:            5px;
$size-handler-height:           100%;
$size-handler-padding:          0 5px;
$size-handler-top-position:     0;
$size-handler-side-position:    -12px;
$size-handler-hover:            $table-bg;

// Sidebar Resize Content
$content-resize-padding:        15px;


//
// Filter Box
// http://ui-toolkit.rdstation.com.br/front-end/filter-box
// --------------------------------------------------

$filter-box-background-color: #f4f5f8;
$filter-box-padding: 11px;
$filter-box-border-color: #d9dde4;
$filter-box-border-style: solid;
$filter-box-border-width: 1px;
$filter-box-border-radius: 3px;

$filter-results-size: 14px;
$filter-results-color: $gray-medium-light;
$filter-results-margin: 11px;

@import 'bootstrap/variables';
