.templates-preview-modal {
  display: none;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;

  &.visible {
    display: block;
  }

  &.desktop .preview {
    width: 100%;
  }

  &.mobile .preview {
    width: 480px;
  }

  .header-navbar {
    background-color: #f2f2f2;
    border: 0;
    height: 46px;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1030;
  }

  .header-navbar-container {
    color: rgba(0, 0, 0, 0.75);
    font-size: 14px;

    a {
      color: #31a2ff;
    }
  }

  .header-navbar-item {
    float: left;
    padding: 0 15px;

    &.right {
      float: right;
      margin-left: 15px;
    }

    .btn-group {
      margin-top: 7px;
    }
  }

  .header-navbar-link {
    background-color: transparent;
    border: 0;
    color: #31a2ff;
  }

  .header-navbar-text {
    display: block;
    float: left;
    line-height: 45px;
    margin: 0;
    position: relative;
  }

  .brand-rdstation {
    float: left;
    padding: 11px 0 5px;

    &-logo {
      height: 24px;
      width: auto;
    }

    .st0 {
      fill: #364a65 !important;
    }
  }

  .preview-title {
    float: right;
    margin-left: 10px;
    max-width: 350px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .content {
    background-color: #e5e5e5;
    height: 100%;
    padding: 46px 0 0 !important;
    position: relative;
  }

  .preview {
    transition: all 0.8s;
  }

  .preview-loading {
    left: 50%;
    position: absolute;
    top: 50%;
    z-index: 1;
  }
}
