$box-shadow: rgba(0, 0, 0, 0.1);
$thumbnail-background-color: #fff;
$thumbnail-border-color: #ddd;
$thumbnail-active-border-color: #31a2ff;
$thumbnail-active-background-color: #f7f7f7;
$template-image-background-color: #f2f2f3;
$template-info-border-top-color: #f2f2f2;
$white-shadow: rgba(255, 255, 255, 0.15);
$white-transparent-background: rgba(250, 250, 250, 0.9);
$dark-gray: #555;
$gray: #777;
$light-gray: #ddd;

.template-card {
  border: 0;
  border-radius: 4px;
  box-shadow: 0 1px 8px $box-shadow;
  display: flex;
  margin: 0;
  overflow: hidden;
  padding: 1px;
  position: relative;
  flex-direction: column;
  text-align: left;
  transition: translate 0.18s, box-shadow 0.18s;
  transform: translateY(0);

  &.thumbnail {
    background-color: $thumbnail-background-color;
    border: 1px solid $thumbnail-border-color;

    &.active {
      border: 1px solid $thumbnail-active-border-color;
      background-color: $thumbnail-active-background-color;
    }
  }

  &:hover {
    box-shadow: 0 5px 25px $box-shadow;
    transform: translateY(-1px);

    .template-overlay {
      box-shadow: inset 0 0 250px $white-shadow;
      background-color: $white-transparent-background;

      .template-info,
      .template-actions {
        display: block;
      }
    }
  }

  &.a11y {
    box-shadow: 0 5px 25px $box-shadow;
    transform: translateY(-1px);

    .template-overlay {
      box-shadow: inset 0 0 250px $white-shadow;
      background-color: $white-transparent-background;

      .template-info,
      .template-actions {
        display: block;
      }
    }
  }


  .template-image {
    background-color: $template-image-background-color;
    display: block;
    height: auto;
    max-width: 100%;
    position: relative;
    width: 100%;
    z-index: 1;

    img {
      width: 100%;
      max-width: 100%;
      border-radius: 4px 4px 0 0;
    }

    .template-info-status {
      bottom: -10px;
      left: 20px;
      padding: 0.3em 0.6em 0.2em;
      position: absolute;
    }
  }

  .template-info {
    padding: 20px;
    border-top: 1px solid $template-info-border-top-color;

    .template-info-name {
      display: block;
      font-size: 16px;
      color: $dark-gray;
    }

    .template-info-author {
      display: block;
      font-size: 14px;
      color: $gray;
      min-height: 19px;
    }
  }

  .template-labels {
    bottom: 20px;
    position: absolute;
    right: 20px;
    z-index: 5;

    > * {
      float: right;
    }
  }

  .template-data {
    display: none;
  }

  .template-name {
    padding: 20px;
  }
}

.template-overlay {
  background-color: transparent;
  border-radius: 4px;
  box-shadow: inset 0 0 1px $white-shadow;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  max-width: 100%;
  padding: 20px;
  position: absolute;
  text-align: center;
  transition: all 0.2s ease-out;
  width: 100%;
  z-index: 10;

  .template-info {
    display: none;
    border: 0;
    padding: 0 0 20px;
    line-height: 20px;

    .template-info-name {
      display: block;
      font-size: 20px;
      line-height: 1.75;
      margin-top: 20px;
    }

    .template-info-author {
      display: block;
      font-size: 14px;
    }
  }

  .template-actions {
    display: none;
    border-top: 1px dashed $light-gray;
    padding-top: 20px;

    .btn {
      margin: 0;
      width: auto;

      + .btn {
        margin-left: 10px;
      }
    }
  }
}
