@import "rdstation/variables";
@import "rdstation/mixins";

// builder
$builder-padding-top:                    125px;
$builder-padding-top-responsive-md:      175px;

// builder header
$builder-header-bg:                      $brand-secondary;
$builder-header-border-color:            $brand-secondary;
$builder-header-border-width:            0 0 1px;
$builder-header-border-style:            solid;
$builder-header-position-top:            0;
$builder-header-position-right:          $builder-header-position-top;
$builder-header-position-left:           $builder-header-position-top;
$builder-header-zindex:                  999;

// builder subheader
$builder-subheader-width:                100%;
$builder-subheader-bg:                   $page-header-bg;
$builder-subheader-position-top:         51px;
$builder-subheader-padding:              15px 15px;
$builder-subheader-border-color:         $sidebar-border-color;
$builder-subheader-border-bottom:        1px solid $builder-subheader-border-color;
$builder-subheader-pseudo-line-height:   0;
$builder-subheader-zindex:               1040;

// builder navbar link
$navbar-link-font-size:                  14px;
$navbar-link-border-color:               rgba(255,255,255,0.18);
$navbar-link-padding:                    0 15px;
$navbar-link-border-width:               0 0 0 1px;
$navbar-link-border-style:               solid;

// builder navbar header
$navbar-header-breadcrumb-margin-right:                    $sidebar-padding;
$navbar-header-breadcrumb-responsive-width:                100%;
$navbar-header-breadcrumb-responsive-border-width:         1px 0 0 0;
$navbar-header-breadcrumb-responsive-border-style:         solid;
$navbar-header-breadcrumb-responsive-border-color:         $navbar-link-border-color;
$navbar-header-breadcrumb-responsive-first-border-style:   none;
$navbar-header-actions-position-top:                       0;
$navbar-header-actions-position-right:                     $navbar-header-actions-position-top;
$navbar-header-actions-position-right:                     $navbar-header-actions-position-top;
$navbar-header-actions-right-margin-right:                 0;
$navbar-header-actions-right-margin-left:                  $sidebar-padding;

// builder steps
$steps-responsive-width:                 0;
$steps-responsive-margin-left:           -12px;
$steps-responsive-text-indent:           -999px;
$steps-responsive-active-width:          auto;
$steps-responsive-active-margin-left:    $steps-responsive-width;
$steps-responsive-active-text-indent:    $steps-responsive-width;

// builder steps responsive
@media only screen and (max-width: 1200px) {
  .steps-subheader-responsive {
    .steps {
      > li {
        > span {
          width: $steps-responsive-width;
          margin-left: $steps-responsive-margin-left;
          text-indent: $steps-responsive-text-indent;
        }
      }
      > li.active {
        > span {
          width: $steps-responsive-active-width;
          margin-left: $steps-responsive-active-margin-left;
          text-indent: $steps-responsive-active-text-indent;
        }
      }
    }
  }
}

// navbar md size
@media only screen and (max-width: 768px) {
  .navbar-container {
    position: relative;
  }
  .navbar-container   {
    .navbar-header-breadcrumb {
      display: none;
    }
  }
  .navbar-header-breadcrumb-responsive {
    width: $navbar-header-breadcrumb-responsive-width;
    border-width: $navbar-header-breadcrumb-responsive-border-width;
    border-style: $navbar-header-breadcrumb-responsive-border-style;
    border-color: $navbar-header-breadcrumb-responsive-border-color;
    a:first-child {
      border-left: none;
    }
  }
  .navbar-header-actions {
    position: absolute;
    top: $navbar-header-actions-position-top;
    right: $navbar-header-actions-position-right;
  }
}

html.template-fullsize,
body.template-fullsize,
#content.template-fullsize {
  height: 100%;
  overflow: hidden;
  position: relative;
  padding-bottom: 0;
}

body.body-builder {
  padding-top: 51px;
  padding-bottom: 0;
}

.content-editor {
  position: relative;
  height: 100%;
}

.sidebar-full {
  top: 0;
  height: 100vh;
  position: fixed;
}

section#content {
  padding: 64px 0 67px;
}

section#content {
  padding-top: 63px;
}

.content-editor {
  > div {
    overflow: auto;
    height: calc(100vh - 114px);
  }
}

.sidebar.sidebar-split,
.sidebar.sidebar-split + .content-split {
  height: calc(100vh - 114px);
}

// builder
#builder {
  padding-top: $builder-padding-top;
}

// builder subheader
.builder-subheader {
  position: fixed;
  top: $builder-subheader-position-top;
  z-index: $builder-subheader-zindex;
  background-color: $builder-subheader-bg;
  padding: $builder-subheader-padding;
  width: $builder-subheader-width;
  border-bottom: $builder-subheader-border-bottom;
}

.steps-subheader:after,
.builder-subheader:after {
  clear: both;
}

.builder-subheader:after,
.builder-subheader:before {
  content: "";
  display: table;
  line-height: $builder-subheader-pseudo-line-height;
}

.navbar-header-breadcrumb {
  float: left;
  margin-right: $navbar-header-breadcrumb-margin-right;
}

.header-navbar-form,
.header-navbar-link,
.header-navbar-text {
  padding: $navbar-link-padding;
  border-width: $navbar-link-border-width;
  border-style: $navbar-link-border-style;
  border-color: $navbar-link-border-color;
}

.navbar-header-actions-right {
  float: right;
  margin-right: $navbar-header-actions-right-margin-right;
  margin-left: $navbar-header-actions-right-margin-left;
}

.actions-subheader {
  float: right;
  margin-right: 0;
  padding-left: 0;
}

// builder header
.builder-header {
  position: fixed;
  top: $builder-header-position-top;
  right: $builder-header-position-right;
  left: $builder-header-position-left;
  z-index: $builder-header-zindex;
  border-width: $builder-header-border-width;
  border-style: $builder-header-border-style;
  background-color: $builder-header-bg;
  border-color: $builder-header-border-color;
  .header-navbar-link {
    color: $navbar-default-link-color;
    font-size: $navbar-link-font-size;
  }
}

.builder-header:before,
.builder-header:after {
    content: " ";
    display: table;
}

.header-navbar-link {
  margin: 0;
  line-height: 50px;
  position: relative;
  display: block;
  max-width: 460px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  float: left;
}
.navbar-container {
  padding: 0;
}

// builder steps
.steps-go-back,
.steps {
  float: left;
}
.steps-sm {
  padding: 5px 10px;
  font-size: 14px;
  line-height: 1.5;
  border-radius: 2px;
}
.steps-subheader {
  margin-bottom: -5px;
  display: inline-block;
  padding-left: 15px;
}
.steps-subheader:after,
.steps-subheader:before {
  content: "";
  display: table;
  line-height: 0;
}
.steps-subheader {
  .steps {
    border-radius: 3px;
    list-style: none;
    position: relative;
    left: 0;
    display: block;
    float: left;
    margin: 0;
    overflow: hidden;
    padding: 0;
  }
}
.steps-subheader {
  .steps {
    > li {
      float: left;
      position: relative;
    }
  }
}
.steps-subheader {
  .steps {
    @for $i from 1 through 5 {
      > li:nth-child(#{$i}) {
        z-index: 10 - $i;
      }
    }
  }
}
.steps-subheader {
  .steps {
    > li:first-child {
      > span {
        padding-left: 15px;
      }
    }
    > li:last-child {
      > span {
        padding-right: 20px;
      }
    }
    > li {
      > span {
        font-size: 1em;
        line-height: 1.4em;
        display: block;
        float: none;
        padding: 7px 7px 7px 32px;
        text-decoration: none;
        margin-right: 0;
        background: $gray-lighter;
      }
    }
    > li {
      > span:before {
        content: "";
        text-decoration: none;
        z-index: -1;
        display: block;
        position: absolute;
        top: -0.4em;
        width: 3.3em;
        height: 3em;
        border-radius: 6px;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        left: auto;
        right: -1em;
        background-color: $gray-lighter;
        border: 2px solid #ffffff;
        border-bottom: none;
        border-left: none;
      }
    }
  }
}
.steps-subheader {
  .steps {
    > li.active {
      > span,
      > span:before {
        background-color: #d0d0d0;
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  #builder {
    padding-top: $builder-padding-top-responsive-md;
  }
  .builder-subheader {
    padding: 50px 15px;
    .vertical-division {
      &:before {
        height: 10px;
        top: -4px;
      }
      &:after {
        height: 10px;
        bottom: -6px;
      }
    }
  }
  .steps-subheader {
    width: 100%;
    padding-left: 0;
    margin-top: -30px;
    margin-bottom: 15px;
    .steps {
      > li {
        > span {
          width: auto;
          margin-left: 0;
          text-indent: 0;
        }
      }
    }
  }
  .steps-go-back {
    position: absolute;
    left: 15px;
    bottom: 20px;
  }
  .actions-subheader {
    position: absolute;
    right: 15px;
    bottom: 20px;
  }
}
@media only screen and (max-width: 640px) {
  .steps-subheader {
    .steps {
      > li {
        > span {
          width: 0;
          margin-left: -12px;
          text-indent: -999px;
        }
      }
    }
  }
  .steps-subheader {
    .steps {
      > li.active {
        > span {
          width: auto;
          margin-left: 0;
          text-indent: 0;
        }
      }
    }
  }
}

// builder sidebar
#builder {
  .sidebar {
    margin-top: 125px;
    height: calc(100vh - 125px);
  }
  .sidebar-split,
  .sidebar-resize {
    margin-top: 0;
  }
}
@media only screen and (max-width: 768px) {
  #builder {
    .sidebar {
      margin-top: 176px;
      height: calc(100vh - 176px);
    }
    .sidebar-split,
    .sidebar-resize {
      margin-top: 0;
    }
  }
}

// vertical division helper
.vertical-division {
  position: relative;
  border-right: 1px solid #ddd;
  padding-right: 20px;
  margin-right: 15px;
  &:before {
    content: '';
    display: block;
    position: absolute;
    border-right: 1px solid #ddd;
    height: 60px;
    top: -60px;
    right: -1px;
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    border-right: 1px solid #ddd;
    height: 30px;
    bottom: -27px;
    right: -1px;
  }
}


$size-handler-bg:               $page-header-bg;
$size-handler-width:            5px;
$size-handler-height:           100%;
$size-handler-padding:          0 5px;
$size-handler-top-position:     0;
$size-handler-side-position:    -12px;
$size-handler-hover:            $table-bg;
$content-resize-width:          calc(100% - #{$sidebar-width});
$content-resize-padding:        15px;


// Sidebar size handler
.sidebar-size-handler {
  cursor: col-resize;
  position: absolute;
  height: $size-handler-height;
  width: $size-handler-width;
  //background: $size-handler-bg image-url('../../images/icons/ic_draggable.png') no-repeat center;
  border-left: $sidebar-border-width solid $main-content-border-color;
  border-right: $sidebar-border-width solid $main-content-border-color;
  padding: $size-handler-padding;
}

.sidebar-right {
  .sidebar-size-handler {
    top: $size-handler-top-position;
    left: $size-handler-side-position;
  }
}

.sidebar-left {
  .sidebar-size-handler {
    top: $size-handler-top-position;
    right: $size-handler-side-position;
  }
}

.sidebar-size-handler:hover {
  background-color: $size-handler-hover;
}

// Sidebar resize
.sidebar.sidebar-resize {
  display: inline-block;
  box-sizing: border-box;
  position: relative;

  +.content-resize {
    display: inline-block;
    box-sizing: border-box;
    width: $content-resize-width;
    transition: $sidebar-transition-width;
    overflow-y: visible;
    transition: none;
  }
}

.content-resize {
  padding-left: $content-resize-padding;
  padding-right: $content-resize-padding;
}

$color-white: #fff;
$color-grey: #ccc;
$color-brigth-grey: #ddd;

hr.spacer-sm {
  margin-top: 10px;
  margin-bottom: 10px;
}

.sidebar-content-subbar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  transition-property: -webkit-transform,transform,opacity,margin;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}

.sidebar-content-subbar.inactive {
  margin-left: -420px !important;
  transition: margin 0.3s ease-out;
}

.sidebar-content-subbar-content {
  position: absolute;
  top: 0;
  background: $color-white;
  border-right: 1px solid $color-brigth-grey;
  box-shadow: 0 0 15px $color-brigth-grey;
  height: calc(100% - 66px);
  width: 100%;
  padding-top: 15px;
  overflow: auto;
}

$color-footer-subbar: #f4f5f8;
$color-footer-brighter-subbar: #d9dde4;

.sidebar-content-subbar-footer {
  background-color: $color-footer-subbar;
  border-top: 1px solid $color-footer-brighter-subbar;
  padding: 15px;
  bottom: 0;
  position: absolute;
  text-align: right;
  width: 100%;
}

.box-group {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  -webkit-flex-flow: column;
  position: relative;
}

.box-group .box-group-item {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  padding-bottom: 15px;
  overflow: hidden;
  width: 100%;
}

.box-group .box-group-item .text-container.clickable {
  cursor: pointer;
}

.box-group .box-group-item .text-container {
  text-decoration: none;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  transition: all 0.3s linear;
  border: 1px solid $color-grey;
  border-radius: 3px;
  position: relative;
  padding: 15px;
  margin-bottom: 0;
  width: inherit;
}

.box-group .box-group-item .text-container {
  text-decoration: none;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  transition: all 0.3s linear;
  border: 1px solid $color-grey;
  border-radius: 3px;
  position: relative;
  padding: 15px;
  margin-bottom: 0;
  width: inherit;
}

.box-group .box-group-item .text-container-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  background: $color-white;
  float: left;
  z-index: 15;
  opacity: 0.2;
  cursor: not-allowed;
}

.box-group .box-group-item .text-container:focus,
.box-group .box-group-item .text-container:hover,
.box-group .box-group-item .text-container.selected {
  background-color: #f3f9ff;
  border-color: #31a2ff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.15);
}
