@import "./commons/misc";
@import "./commons/template-card";
@import "./commons/template-list";
@import "./quill-custom";
@import "./preview";

$overlay-color: rgba(255, 255, 255, 0.5);
$spinner-background-color: #fff;
$step-font-color: #ccc;

.embeddables-app {
  margin-top: 122px;
}

.navbar-brand {
  padding: 10px 24px;
  line-height: 22px;
  height: 45px;
}

.header-navbar-link {
  line-height: 45px;
}

.builder-subheader {
  top: 46px;

  .actions-subheader {
    .subheader-btn + .subheader-btn {
      margin-left: 12px;
    }
  }

  .step {
    color: $step-font-color;

    a {
      cursor: pointer;
    }

    &.active {
      color: inherit;
    }

  }

}

.container-templates {
  align-items: stretch;
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  list-style: none;
  margin: -10px;
  padding: 0;
}

.container-fluid.main-content {
  padding: 0 18px;
  margin: 140px auto;

  @media screen and (min-width: 1480px) {
    width: 1480px;
  }

}

.container-templates-item {
  flex-basis: 50%;
  padding: 10px;
}

.loading-overlay {
  position: relative;

  &::after {
    display: block;
    content: "";
    background: $overlay-color;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 2;
  }
}

.centered-spinner-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: $spinner-background-color;
}

.go-forward.editor {
  width: auto;
  min-width: 150px;

  &.subheader-btn.disabled.saving::before {
    content: "";
  }
}
